<template>
  <div id="vidContainer" v-if="this.show == 3">
    <h2>{{ pInfo.name }} Videos</h2>
    <div
      class="video"
      v-for="(video, index) in this.videoData"
      v-bind:key="index"
      @click="getVideoInfo(video)"
    >
      <img :src="video.thumbnail" />
      <div class="videoInfo">
        <h3>{{ video.videoTitle }}</h3>
        <h4>{{ video.channel }}</h4>
        <p id="desc">{{ video.description }}</p>
        <p>Released on: {{ video.date.slice(0, 10) }}</p>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "videosApp",
  props: {
    pInfo: Object,
    show: Number,
  },
  data() {
    return {
      playlistName: "the playlist u clicked",
      videoData: [],
    };
  },
  methods: {
    async getVideos() {
      //send pInfo.id to youtube api
      this.videoData = [];
      //axios api communication
      const dataResponse = await axios.get(
        "https://youtube.googleapis.com/youtube/v3/playlistItems?part=snippet",
        {
          params: {
            maxResults: 10,
            playlistId: this.pInfo.id,
            key: "AIzaSyCobiuzkt92UYIC89TdAmVOwicq0RcAhRw",
          },
        }
      );
      this.testData = dataResponse.data.items;
      this.testData.forEach((element) => {
        this.videoData.push({
          thumbnail: element.snippet.thumbnails.medium.url,
          videoId: element.snippet.resourceId.videoId,
          videoTitle: element.snippet.title,
          channel: element.snippet.channelTitle,
          description: element.snippet.description,
          date: element.snippet.publishedAt,
        });
      });
    },
    getVideoInfo(c) {
      this.$emit("info", {
        id: c.videoId,
        name: c.videoTitle,
      });
    },
  },
};
</script>
<style scoped>
#vidContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.video {
  display: flex;
  margin: 8px;
  border-radius: 10px;
  width: 75%;
  background-color: #d1cece;
  cursor: pointer;
}
img {
  width: 40%;
  border-radius: 10px;
}
.videoInfo {
  width: 60%;
}
#desc {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
</style>