<script>
import playlistAppVue from "./components/playlistApp.vue";
import videosAppVue from "./components/videosApp.vue";
import videoPlayerVue from "./components/videoPlayer.vue";
export default {
  name: "App",
  components: {
    playlists: playlistAppVue,
    videos: videosAppVue,
    player: videoPlayerVue,
  },

  data() {
    return {
      query: "",
      q: "",
      s: 1,
      selectedPlaylist: {
        id: "",
        name: "",
      },
      selectedVideo: {
        id: "",
        name: "",
      },
    };
  },
  methods: {
    initSearch() {
      if (this.query != "" && this.query !== " ") {
        this.q = this.query;
        this.s = 2;
        this.$refs.pl.fetchPlaylistFacts(this.query);
        this.query = "";
      }
    },
    navigation() {
      this.s = this.s - 1;
    },
    sendPlaylistInfo(i) {
      this.selectedPlaylist.id = i.id;
      this.selectedPlaylist.name = i.name;
      this.s = 3;
      this.$refs.vid.getVideos();
    },
    sendVideoInfo(i) {
      this.selectedVideo.id = i.id;
      this.selectedVideo.name = i.name;
      this.s = 4;
      this.$refs.play.getMyVideo();
    },
  },
};
</script>

