<template>
  <div id="main" v-if="this.show == 2">
    <h2>
      Playlists based on
      <p>&apos;{{ link }}&apos;</p>
    </h2>
    <div
      v-for="(playlist, index) in this.playlistData"
      v-bind:key="index"
      class="playlist"
      @click="getPlaylistInfo(playlist)"
    >
      <img :src="playlist.thumbnail" />
      <div class="playlistInfo">
        <h3>{{ playlist.playlistTitle }}</h3>
        <h4>{{ playlist.channel }}</h4>
        <p id="desc">{{ playlist.description }}</p>
        <p>Curated on: {{ playlist.date.slice(0, 10) }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "playlistApp",
  testData: [],
  props: {
    link: String,
    show: Number,
  },
  data() {
    return {
      playlistData: [],
    };
  },
  methods: {
    getPlaylistInfo(c) {
      this.$emit("info", {
        id: c.playlistId,
        name: c.playlistTitle,
      });
    },
    async fetchPlaylistFacts(q) {
      this.playlistData = [];
      //axios api communication
      const dataResponse = await axios.get(
        "https://youtube.googleapis.com/youtube/v3/search?part=snippet",
        {
          params: {
            maxResults: 10,
            q: q,
            type: "playlist",
            key: "AIzaSyCobiuzkt92UYIC89TdAmVOwicq0RcAhRw",
          },
        }
      );
      this.testData = dataResponse.data.items;
      //arranging returned data for display in the dom
      this.testData.forEach((element) => {
        this.playlistData.push({
          thumbnail: element.snippet.thumbnails.medium.url,
          playlistId: element.id.playlistId,
          playlistTitle: element.snippet.title,
          channel: element.snippet.channelTitle,
          description: element.snippet.description,
          date: element.snippet.publishedAt,
        });
      });
    },
  },
};
</script>

